/* @group Base */
.chosen-container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  float: right; }
  .chosen-container:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    border-bottom: 2px solid #e60028;
    width: 0;
    will-change: width;
    transition: all .3s linear;
    -o-transition: all .3s linear;
    -moz-transition: all .3s linear;
    -webkit-transition: all .3s linear;
    z-index: 6; }
  .chosen-container.chosen-container-active:before {
    width: 100%; }
  .chosen-container .chosen-drop {
    left: 0;
    top: 100%;
    opacity: 0;
    z-index: 3;
    width: 100%;
    display: block;
    margin-top: 0;
    position: absolute;
    visibility: hidden;
    letter-spacing: 2.5px;
    font-size: 17px;
    transition: all 0.3s ease;
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.1);
    border: 0;
    background-color: #fff; }
  .chosen-container.chosen-with-drop .chosen-drop {
    opacity: 1;
    visibility: visible;
    transform: translateY(7px); }
  .chosen-container a {
    cursor: pointer; }
  .chosen-container .search-choice .group-name, .chosen-container .chosen-single .group-name {
    margin-right: 4px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: normal;
    color: #999999; }
    .chosen-container .search-choice .group-name:after, .chosen-container .chosen-single .group-name:after {
      content: ":";
      padding-left: 2px;
      vertical-align: top; }

/* @end */
/* @group Single Chosen */
.chosen-container-single .chosen-single {
  z-index: 5;
  width: 100%;
  display: block;
  cursor: pointer;
  position: relative;
  padding: 0px 36px 0px 0;
  height: 54px;
  transition: all .2s ease-in-out 0s;
  -o-transition: all .2s ease-in-out 0s;
  -moz-transition: all .2s ease-in-out 0s;
  -webkit-transition: all .2s ease-in-out 0s;
  text-decoration: none;
  background-color: #fff;
  font-size: em(19);
  border-bottom: 2px solid #d9d9d9; }

.chosen-container-single .chosen-default {
  color: #999; }

.chosen-container-single .chosen-single span {
  display: block;
  overflow: hidden;
  margin-right: 26px;
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: 1px;
  line-height: normal;
  transition: all .1s ease 0s;
  -o-transition: all .1s ease 0s;
  -moz-transition: all 1s ease 0s;
  -webkit-transition: all .1s ease 0s;
  padding: 14px 0; }

.chosen-container-single .chosen-single-with-deselect span {
  margin-right: 38px; }

.chosen-container-single .chosen-single abbr {
  position: absolute;
  top: 6px;
  right: 26px;
  display: block;
  width: 12px;
  height: 12px;
  background: image-url("chosen-sprite.png") -42px 1px no-repeat;
  font-size: 1px; }
  .chosen-container-single .chosen-single abbr:hover {
    background-position: -42px -10px; }

.chosen-container-single.chosen-disabled .chosen-single abbr:hover {
  background-position: -42px -10px; }

.chosen-container-single .chosen-single div {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 18px;
  height: 100%; }
  .chosen-container-single .chosen-single div b {
    display: block;
    width: 100%;
    height: 100%;
    background: image-url("chosen-sprite.png") no-repeat 0px 2px; }

.chosen-container-single .chosen-search {
  position: relative;
  z-index: 1010;
  margin: 0;
  padding: 3px 4px;
  white-space: nowrap; }
  .chosen-container-single .chosen-search input[type="text"] {
    margin: 1px 0;
    padding: 4px 20px 4px 5px;
    width: 100%;
    height: auto;
    outline: 0;
    border: 1px solid #aaa;
    background: #fff image-url("chosen-sprite.png") no-repeat 100% -20px;
    /*@include background($chosen-sprite no-repeat 100% -20px);*/
    font-size: 1em;
    font-family: sans-serif;
    line-height: normal;
    border-radius: 0; }

.chosen-container-single .chosen-drop {
  margin-top: 0;
  border-radius: 0;
  background-clip: padding-box; }

.chosen-container-single.chosen-container-single-nosearch .chosen-search {
  position: absolute;
  left: -9999px; }

/* @end */
/* @group Results */
.chosen-container .chosen-results {
  color: #000;
  font-size: 19px;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0;
  padding: 0;
  max-height: 280px;
  -webkit-overflow-scrolling: touch; }
  .chosen-container .chosen-results li {
    display: none;
    margin: 0;
    padding: 20px 30px;
    list-style: none;
    word-wrap: break-word;
    -webkit-touch-callout: none;
    border-bottom: 1px solid #e5e5e5;
    transition: all 0.3s ease; }
    .chosen-container .chosen-results li:last-child {
      border: 0; }
    .chosen-container .chosen-results li.active-result {
      display: list-item;
      cursor: pointer; }
    .chosen-container .chosen-results li.disabled-result {
      display: list-item;
      color: #ccc;
      cursor: default;
      display: none !important; }
    .chosen-container .chosen-results li.highlighted {
      background-color: #fafafa;
      /*@include background-image(linear-gradient(#3875d7 20%, #2a62bc 90%));*/
      color: #e60028; }
    .chosen-container .chosen-results li.no-results {
      color: #777;
      display: list-item;
      background: #f4f4f4; }
    .chosen-container .chosen-results li.group-result {
      display: list-item;
      font-weight: bold;
      cursor: default; }
    .chosen-container .chosen-results li.group-option {
      padding-left: 15px; }
    .chosen-container .chosen-results li em {
      font-style: normal;
      text-decoration: underline; }

/* @end */
/* @group Multi Chosen */
.chosen-container-multi .chosen-choices {
